nav {
  display: flex;
  justify-content: center;
}
ul {
  display: flex;
  width: 500px;
  height: 60px;
  border-radius: 20px;
  background-color: rgb(154, 154, 228);
  justify-content: space-around;
  align-items: center;
  padding: 0;
}
li {
  display: flex;
  width: 40%;
  height: 70%;
  background-color: rgb(181, 181, 233);
  justify-content: center;
  align-items: center;
  list-style: none;
  border-radius: 10px;
}

li * {
  text-decoration: none;
  color: white;
  font-weight: bolder;
}
